.ILoveBootstrapIfuckCodepen {
  color: #fff;
  text-align: center;
  letter-spacing: 1px;
  cursor: pointer;
  z-index: 1;
  background: #4c00fe;
  border: 0;
  border-radius: 5px;
  width: 145px;
  height: 40px;
  font-size: 14px;
  font-weight: bold;
  line-height: 40px;
  transition: transform .3s ease-in-out;
  display: block;
  position: relative;
  overflow: hidden;
}

.ILoveBootstrapIfuckCodepen:before {
  content: "";
  z-index: -1;
  background: linear-gradient(90deg, #0000, #ffffff80, #0000);
  width: 300%;
  height: 100%;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: -100%;
  transform: skewX(-45deg);
}

.ILoveBootstrapIfuckCodepen:hover {
  transform: scale(1.1);
}

.ILoveBootstrapIfuckCodepen:hover:before {
  transition: all .5s;
  left: 100%;
}

@keyframes glow {
  0% {
    box-shadow: 0 0 10px #4c00fe, 0 0 20px #4c00fe;
  }

  100% {
    box-shadow: 0 0 20px #4c00fe, 0 0 40px #4c00fe;
  }
}

.ILoveBootstrapIfuckCodepen {
  animation: 1.5s infinite alternate glow;
}

.cardFullscreenTextContainer {
